.wp-block-blocks-course-team-members {
	display: block;

	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			> .block-editor-inner-blocks > .block-editor-block-list__layout > {
				[data-type="blocks-course/team-member"] {
					width: calc(100% / #{$i} - 20px);
				}

				.block-list-appender {
					align-self: stretch;
					width: calc(100% / #{$i} - 20px);
					border: 1px dashed;
					display: flex;
					align-items: center;
					justify-content: center;
					min-height: 70px;
					margin-left: 10px;
					position: relative;
				}
			}
		}
	}

	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;
		column-gap: 20px;

		[data-type="blocks-course/team-member"] {
			max-width: none;
			margin: 0;
			padding-left: 2px;
			.block-editor-media-placeholder {
				margin-bottom: 20px;
			}
		}

		> [data-type="blocks-course/team-member"] {
			// img upload
			.wp-block-blocks-course-team-img {
				position: relative;
				margin-bottom: 20px;
				&.is-loading img {
					opacity: 0.3;
				}
				.components-spinner {
					position: absolute;
					inset: 0;
					margin: auto;
				}
			}
			h4 {
				color: #222;
				font-size: 22px;
				font-size: bold;
				margin: 0;
			}

			p {
				color: #565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px;
			}

			.wp-block-blocks-course-team-social-links {
				ul {
					list-style: none;
					padding: 0;
					margin: 15px -5px 0;
					li {
						display: inline-block;
						margin: 5px;
						span.dashicon {
							color: #4e4e4e;
						}
						&.is-selected {
							outline: 2px solid purple;
						}
						button {
							border: none;
							background: none;
							padding: 0;
							cursor: pointer;
							margin: 0;
						}
						&.wp-block-blocks-course-team-add-icon-li {
							button {
								outline: 1px dashed;
							}
						}
					}
				}
			}
		}
	}
	&.block-editor-block-list__block
		.block-list-appender
		.block-list-appender__toggle {
		display: block;
	}
}
